import React, { useState } from "react";
import ItemsList from "./subcomponents/ItemsList";
import ItemsShowList from "./subcomponents/ItemsShowList";
import WebMap from "./subcomponents/WebMap";

const Shop = () => {
  const [category, setCategory] = useState("all");

  return (
    <div className="container">
      <WebMap category={category} />
      <main className="row">
        <ItemsList setCategory={setCategory} />
        <ItemsShowList category={category} />
      </main>
    </div>
  );
};

export default Shop;
